$screen-sm: 768px;
$screen-md: 992px;
$screen-lg: 1200px;
$screen-xlg: 1600px;

@mixin xs {
  @media (max-width: 767px) {
    @content; } }

@mixin sm {
  @media (min-width: #{$screen-sm}) {
    @content; } }

@mixin md {
  @media (min-width: #{$screen-md}) {
    @content; } }

@mixin lg {
  @media (min-width: #{$screen-lg}) {
    @content; } }

@mixin xlg {
  @media (min-width: #{$screen-xlg}) {
    @content; } }
